import * as React from 'react';


import { 
  Container, 
  Main,
} from '../../styles/Layout';

import {
        WrapperCartao,
        Logo,
        WrapperPerfil,
        WrapperAvatar,
        WrapperPerfilInfo,

        WrapperCardMedia,

        WrapperServico,
        WrapperGrupoServico,
        CardServico, 

        useStyles,
  } from './styles';

import Avatar from '@material-ui/core/Avatar';


import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';


import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';

import EmailIcon from '@material-ui/icons/Email';
import PhoneIcon from '@material-ui/icons/Phone';
import RoomIcon from '@material-ui/icons/Room';

import {
  Link
} from "react-router-dom";

import { useGenericModel } from '../../hooks';
import { HeadContext } from '../../contexts';

function Home({match, location }) {

  const classes = useStyles();

  const [account, setAccount, AccountInicial] = useGenericModel('account_profile', match?.params.a);
  const { head, setHead } = React.useContext(HeadContext);

  React.useEffect( () => {
      if(account){
        setHead(account)
      }
  },[account])


  return (
    <>
    {
      account?.id ?
        <Container>
        <Main>
            <WrapperCartao color={account.user.cartao?.cor} >
                <Logo src={account.user.cartao?.logo}/> 
            </WrapperCartao>

        <Accordion >
          <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
            <WrapperPerfil >
                  <WrapperAvatar>
                    <Avatar alt="Diego Fontes" src={account.user.profile?.avatar} className={classes.avatar} />
                  </WrapperAvatar>
                  <WrapperPerfilInfo >
                      <Typography variant="h6">
                          {account.user?.first_name}
                      </Typography>
                      <Typography variant="caption" color="textSecondary">
                        {account.user.profile?.profissao}
                      </Typography>
                  </WrapperPerfilInfo>
              </WrapperPerfil>
          </AccordionSummary>
        <AccordionDetails>
                     
          <List>
              <ListItem>
                <ListItemAvatar>
                    <EmailIcon />
                </ListItemAvatar>
                <ListItemText primary="E-mail" secondary={account.user?.email} />
              </ListItem>

              <ListItem>
                <ListItemAvatar>
                    <PhoneIcon />
                </ListItemAvatar>
                <ListItemText primary="Telefone" secondary={account.user.profile?.telefone} />
              </ListItem>

              <ListItem>
                <ListItemAvatar>
                    <RoomIcon />
                </ListItemAvatar>
                <ListItemText primary="Endereo" secondary={account.user.profile?.endereco} />
              </ListItem>

          </List>

        </AccordionDetails>
      </Accordion>
        <CardContent >
              <Typography variant="caption" gutterBottom>
                  SERVIOS
              </Typography>
        </CardContent>
        <WrapperServico>
            {
              account.user?.categoria_servico.map( (categoria_servico, index) => (
                    <WrapperGrupoServico key={index} >
                          <Typography variant="overline" gutterBottom>
                            {categoria_servico.name}
                          </Typography>
                          {categoria_servico.servico.map( (servico, index) => (
                              <Link  key={index}
                               to={{
                                pathname: `${match?.params.a}/s`,
                                // search: "?sort=name",
                                // hash: "#the-hash",
                                state: { 
                                  servico: servico,
                                  categoria: categoria_servico.name,
                                  cartao: account.user.cartao?.cor,
                                }                                
                              }}>
                                <CardServico>
                                    <div>
                                      <CardContent >
                                        <Typography variant="h6">
                                            {servico?.nome}
                                        </Typography>
                                        <Typography variant="body2" color="textSecondary">
                                            {servico?.descricao}
                                        </Typography>
                                      </CardContent>
                                    </div>
                                    <WrapperCardMedia>
                                      <CardMedia
                                        className={classes.cover}
                                        image={servico?.img}
                                        title={servico?.descricao}
                                      />
                                    </WrapperCardMedia>
                              </CardServico>
                            </Link>
                          ))}
                  </WrapperGrupoServico>
              ))
            }
        </WrapperServico>

        </Main>
    </Container>
        :
        <></>
    }
    </>
  );
}

export { Home };
